.search{padding:8px 0;background-color:#000;color:#FFF;width:100%}.search .title{font-size:2.4rem;margin:0px;color:#42b983}.search .title span{color:#D2D2D2}.search .radio-btn{display:inline-block;width:28%;max-width:427px;padding:6px 0;height:19px;background-color:#383838;color:#999999;font-weight:bold;margin:0 4px;position:relative;cursor:pointer;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}@media (max-width: 1204px){.search .radio-btn{width:47%;max-width:none}}.search .radio-btn:after{position:absolute;content:'';width:76%;height:2px;background-color:rgba(243,243,243,0.53);bottom:5px;left:50%;margin-left:-39%;-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.search input[type="radio"]{display:none}.search input{font-size:1.3rem;margin-bottom:8px}.search input:checked+.radio-btn{color:#D2D2D2}.search input:checked+.radio-btn:after{background-color:#41B883}.search .btn-search{cursor:pointer;font-size:1.3rem;color:#FFF;background-color:#5F5E5E;border:solid 2px #5F5E5E}.search .btn-search:hover,.search .btn-search:focus{outline:none;background-color:#717171;border:solid 2px #717171}



